html,
body,
#root {
    position: fixed;
    top:0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    /* background: lightblue; */
}

.label > div {
    font-family:  Helvetica,Arial;
    position: absolute;
    background-color: #00000088;
    color: white;
    padding: 15px;
    white-space: nowrap;
    overflow: hidden;
    border-radius: 30px;
    user-select: none;
}